 .row{
    color: white;
    margin-left: 75px;
 }
 .row_posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
  
}
.row_posters::-webkit-scrollbar{
    display: none;
}

.row_poster{
object-fit: contain;
width: 100%;
max-height: 100px;
margin-right: 10px;
transition: transform 450ms;
} 

.row_poster:hover{
    transform: scale(1.08);
    opacity: 1;
}
.row_posterLarge{
    transition: transform 450ms;
}
.row_posterLarge:hover{
    transform: scale(1.09);
    opacity: 1;
}